*{
  margin: auto;
  font-family:'Raleway', sans-serif;
  font-weight: 400;
  
  }
  
.app-container{
display: flex;
flex-direction: column;
width: 100%;

}

.nav-bar{
  background-color: rgb(147, 178, 153);
  width: 100%;
  height: 88px;
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.shop-cart{
  margin: 0 25px;
}

.hero{
  margin: 25px 0 25px 0;
  text-align: center;
}

h1{
  font-family: 'Sacramento', cursive;
  font-size: 45px;
  letter-spacing: normal;
}

h2{
  font-size: 22px;
  font-weight: 300;
}

h3{
  font-family:'Sacramento', cursive; 
  font-size: 35px;
  margin: 50px auto;
  text-align: center;
  max-width: 600px;
  position: relative;
}

h3::before{
  content: "";
  display: block;
  width: 440px;
  height: 2px;
  background: rgb(226, 226, 226) ;
  position: absolute;
  right: 200px;
  top: 50%;
}

h3::after{
  content: "";
  display: block;
  width: 440px;
  height: 2px;
  background: rgb(226, 226, 226) ;
  position: absolute;
  left: 200px;
  top: 50%;
}
a{
  text-decoration: none;
color: black;
}

.main-img{
  margin: 25px 0 25px 0;
  border: solid 8px rgb(147, 178, 153) ;
}

.gallery{
  display: flex;
  flex-wrap: wrap;
  max-width:90%;
  margin-bottom: 100px;
}

.cat-tile img {
  width: 250px;
  height: 250px;
  margin-top: 30px;
  border-radius: 5px;
}
.tile-cart{
  font-size: 25px;
  cursor: pointer;
}

span{
  font-size: 12px;
  margin-left: 10px;
}

.shop{
  visibility: hidden;
}

.footer{
  display: flex;
  flex-direction: column;
  align-content: center;
  width: 90%;
  height: 100px;
  border-top: 1px rgb(226, 226, 226) solid;
}

@media (max-width: 1050px){
  h3::before{
    width: 350px;
  }
  
  h3::after{
    width: 350px;
  }
}

@media (max-width: 950px){
  h3::before{
    width: 300px;
  }
  
  h3::after{
    width: 300px;
  }
}

@media (max-width: 850px){
  h3::before{
    width: 200px;
  }
  
  h3::after{
    width: 200px;
  }
  .main-img{
    width: 500px;
  }
}

@media (max-width: 600px){
  h3::before{
    width: 150px;
    right: 150px;
  }
  
  h3::after{
    width: 150px;
    left: 150px;
  }
  .main-img{
    width: 400px;
  }
  h1{
    font-size: 30px;
  }
  h2{
    font-size: 15px;
  }
  h3{
    font-size: 25px;
  }
  .cat-tile img {
    width: 200px;
    height: 200px;
    margin-top: 0px;
  }
}

@media (max-width: 450px){
  h3::before{
    width: 50px;
  }
  
  h3::after{
    width: 50px;
  }
  .main-img{
    width: 300px;
  }
  .cat-tile img {
    width: 150px;
    height: 150px;
  }
}

